<script>
import queryListItem from "@/components/common/queryListItem.vue";
// import generateImgItem from "@/components/common/generateImgItem.vue";
import generateImg from "@/components/common/generateImg.vue";
import queryListHeader from "@/components/common/queryListHeader.vue";
import BScroll from "better-scroll";
export default {
  data() {
    return {
      generateImg:false, //生成的图片的控制条
      generateImgPop: false, //生成的图片弹窗
      imgPath: "", //生成的图片的路径
      showImg: false, //显示img还是显示dom
      showBottomTip:true,
      priceStatus:"price",//alert:修改 ， hidden:隐藏 price:显示价格
 
     
    };
  },
  mounted() {
      this.$toast({
          message: '正在查询',
          icon: '/img/loading.png', 
          duration:3000
      })

      this.$refs.myToast.myToast();

      //  this.$toast({
      //     message: '本次操作扣除1积分',
      //     duration:9000
      // })
  },
  components: {
    queryListItem,
    // generateImgItem,
    generateImg,
    queryListHeader
  },
  methods: {
    //关闭弹窗和底部的生成图片按钮
    closeGImgP() {
      this.generateImg = false; //生成的图片的控制条
      this.generateImgPop = false; //生成的图片弹窗
    }, 
    addScroll(){
        var wrapper = document.getElementById("imgW");
        console.log(wrapper);
        this.scrollObj = new BScroll(wrapper, {});
        let scroll = this.scrollObj; 
        //判断图片的高度和外框的高度的关系，然后
        //确定要不要添加滚动监听，要不要显示底部的滚动提示
        let imgH = document.getElementById("generateImg").scrollHeight;
        let boxH = document.getElementById("imgW").clientHeight;

        console.log(imgH,boxH);
        if(imgH - 10 > boxH){
              //添加滚动
              scroll.on('scrollStart', (pos) => {
                this.showBottomTip = false;
              })
              scroll.on('scrollEnd', (pos) => {
                  this.showBottomTip = true;
              })
        }else{
              this.showBottomTip = false;
        }

    },
    toGenerateImg() {
      let that = this;
      this.generateImgPop = true;
      let toast1 = this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 10000
      });

      setTimeout(function() {
        const targetDom = document.getElementById("scrollW");
        const copyDom = targetDom.cloneNode(true);
        copyDom.style.width = targetDom.scrollWidth + "px";
        copyDom.style.height = targetDom.scrollHeight + "px";
        copyDom.id = "copyDom";
        document.body.appendChild(copyDom);

        var opts = {
          allowTaint: false,
          useCORS: true,
          height: targetDom.scrollHeight,
          width: targetDom.scrollWidth,
          background:"#111111"  
        };

        console.log(opts, "参数-------");

        that.html2canvas(copyDom, opts).then(canvas => {
          // removeChild
          // console.log(document.getElementById("scrollW"))
          // let node = document.getElementById("scrollW")
          // document.body.removeChild(node);
          let dataURL = canvas.toDataURL("image/png");
          console.log(dataURL, canvas);
          that.imgPath = dataURL;
          that.showImg = true;
          toast1.clear();

          //删除body 里的dom 节点
          let copyDom = document.getElementById("copyDom");
          document.body.removeChild(copyDom);

          //添加滚动
          // that.$nextTick(() => {
          setTimeout(() => {
              that.addScroll()
          }, 500);
          // })
        });
      }, 300);
    },
    cancelGenerateImg(){
       this.generateImg = false;
       this.priceStatus = "price";
       this.$refs['queryListItem'].priceStatus = "price";
    },
    changeListItem() {},
    //修改价格方法
    // alertPrice(){
    //     this.$refs['queryListItem'].alertPrice();
    //     this.priceStatus = "alert";
    //     this.generateImg = true;
    //     // console.log(this.$refs['queryListItem'])
    // }
  },
  watch:{
      priceStatus(e){
           if(e == 'price'){
              //正常显示价格
                this.$refs['queryListItem'].priceStatus = 'price';
                this.generateImg = false;
              
           }else if(e == 'alert'){
               //修改价格
                this.$refs['queryListItem'].alertPrice();
                this.generateImg = true;

           }else if(e == "hidden"){
              // 隐藏价格
                this.$refs['queryListItem'].priceStatus = 'hidden';
                this.generateImg = false;

           }
      }
  }
};
</script>
